<template>
  <div class="navContainer">
    <button
      @click="isShow = !isShow"
      :class="{ nfp: !$route.meta.ifg }"
      :style="{
        background: btnColor,
        color: fontColor
      }"
      @mouseover="mouseOver"
      @mouseout="mouseOut"
    >
      Discover More
    </button>
    <nav v-if="isShow">
      <ul>
        <li v-for="item in nav" :key=item.text :to=item.link @click="isShow = false">
          <router-link :to=item.link >
            <span>{{ item.text }}</span>
            <icon :name=item.icon />
          </router-link>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import 'vue-awesome/icons/home'
import 'vue-awesome/icons/check'
import 'vue-awesome/icons/graduation-cap'
import 'vue-awesome/icons/briefcase'
import 'vue-awesome/icons/user'

export default {
  name: 'Nav',
  data () {
    return {
      isShow: false,
      nav: [{icon: 'home', text: 'Home', link: '/'},
        {icon: 'check', text: 'Skills', link: '/Skills'},
        {icon: 'graduation-cap', text: 'Education', link: '/Education'},
        {icon: 'briefcase', text: 'Work Exp', link: '/WorkExp'},
        {icon: 'user', text: 'Contact', link: '/Contact'}
      ],
      btnColor: this.$route.meta.color,
      fontColor: '#ecf0f1'
    }
  },
  methods: {
    mouseOver: function () {
      this.btnColor = '#ecf0f1'
      this.fontColor = this.$route.meta.color
    },
    mouseOut: function () {
      this.btnColor = this.$route.meta.color
      this.fontColor = '#ecf0f1'
    }
  }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/main
.navContainer
  button
    text-align: center
    display: block
    color: $white
    transition: all 0.4s ease
    border: 1px solid $white
    outline: none
    padding: 0.8rem 2rem
    cursor: pointer
    margin: 2.5rem auto 0
    +bp(min, 768px)
      margin-left: 0

  button.nfp
    position: fixed
    bottom: 0
    left: 50%
    transform: translateX(-50%)
  nav
    +size(100vw, 100vh)
    background: #666
    position: fixed
    top: 0
    left: 0
    ul
      margin: 0
      padding: 0
      height: 100%
      list-style: none
      padding-left: 0
      display: flex
      flex-direction: column
      justify-content: space-around
      align-items: center
      li
        a
          color: $white
          text-decoration: none

</style>



