<template>
  <div class="container">
    <div class="left">
      <h1>Skills and Abilities</h1>
      <p>{{ msg }}</p>
      <div class="drag"></div>
    </div>
    <div class="right">
      <h1>I Use..</h1>
      <div>
        <ul>
          <li>
            <i class="fab fa-html5"></i>
            <span>HTML5</span>
          </li>
          <li>
            <i class="fab fa-css3"></i>
            <span>CSS3</span>
          </li>
          <li>
            <ul>
              <li>
                <i class="fab fa-sass"></i>
                <span>Sass</span>
              </li>
              <li>
                <i class="fab fa-less"></i>
                <span>Less</span>
              </li>
            </ul>
          </li>
          <li>
            <i class="fab fa-js"></i>
            <span>JS</span>
          </li>
          <li>
            <ul>
              <li>
                <i class="fab fa-react"></i>
                <span>React.js</span>
              </li>
              <li>
                <i class="fab fa-vuejs"></i>
                <span>Vue.js</span>
              </li>
            </ul>
          </li>
          <li>
            <i class="fab fa-wordpress"></i>
            <span>wordpress</span>
          </li>
          <li>
            <i class="fab fa-github"></i>
            <span>Github</span>
          </li>
        </ul>

      </div>
    </div>
    <Nav />
  </div>
</template>

<script>
import Nav from './Nav.vue'

export default {
  name: 'Skills',
  data () {
    return {
      msg: 'As more and more website platforms are released nowadays, I still prefer to make responsive websites using my own Sass code. '
    }
  },
  components: { Nav }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/main
div.container
  display: flex
  justify-content: space-between
  width: 80%
  .left
    width: 60%
    p
      box-sizing: border-box
      color: $white
      font-family: Courier, monospace
      font-weight: normal
      font-size: 24px
      line-height: 40px
      padding: 25px 50px
      margin: 0
      border-radius: 12px
      border: 1px solid $white
  .right
    width: 30%
    ul
      margin: 0
      padding: 0
      list-style: none
      li
        line-height: 2rem
        font-size: 1.2rem
        i
          width: 2rem
          text-align: center
        ul
          li
            margin-left: 2rem
            font-size: 1rem
</style>



