<template>
  <div class="container">
    <div class="homeview">
      <section class="hi">
        Hi<span>!</span>
      </section>
      <section class="welcome">
        <h1>I am Khee</h1>
        <h2>&</h2>
        <h1>I make Websites</h1>
        <Nav />
      </section>

    </div>
  </div>
</template>

<script>
import AboutMe from './AboutMe.vue'
import Close from './Close.vue'
import Nav from './Nav.vue'

export default {
  name: 'Home',
  data () {
    return {
      isShow: false,
      nav: [{icon: 'home', text: 'Home', link: '/'},
        {icon: 'check', text: 'Skills', link: '/Skills'},
        {icon: 'graduation-cap', text: 'Education', link: '/Education'},
        {icon: 'briefcase', text: 'Work Exp', link: '/WorkExp'},
        {icon: 'user', text: 'Contact', link: '/Contact'}
      ]
    }
  },
  components: {AboutMe, Close, Nav}
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/main

.container
  width: 100%
  height: 100vh
  display: flex
  justify-content: center
  align-items: center
  overflow-x: hidden
  overflow-y: scroll
  color: $white
  .homeview
    +bp(min, 768px)
      display: flex
      align-items: flex-start
      flex-wrap: wrap

    .hi
      font-size: 5rem
      box-sizing: border-box
      +bp(min, 768px)
        padding-top: rem-calc(15px)
        padding-right: rem-calc(40px)
      text-align: center
      span
        display: none
        +bp(min, 768px)
          display: inline
    .welcome
      transition: all 0.6s ease
      text-align: center
      h1, h2
        font-family: 'Roboto Slab', serif
        +bp(min, 768px)
          text-align: left

  .detail
    position: relative
    background: $white
    transition: width 0.6s ease
    transition: height 0.6s ease
    +size(0,0)
    border-radius: 50%
    overflow: hidden
    .close
      cursor: pointer
      position: fixed
      right: 3rem
      bottom: 2rem
      .cross-btn
        transition: all 0.4s ease

  .detail.active
    +size(100%, 100vh)
    border-radius: 0
    overflow-y: scroll
</style>



